/**
* Color Definitions
*/
/**
* Topography Definitions
*/
/**
 * Add font scale with media query as per configuration on $body-text
 * Ex: @include body-text( h1 );
 * This will add font-size and line-height with different breakpoints.
 */
body {
  margin: 0;
  font-size: 16px;
  line-height: 1.4;
  background: #edf1f5;
  word-wrap: break-word;
}
@media (min-width: 0em) {
  body {
    font-size: 14px;
    line-height: 1.4;
  }
}
@media (min-width: 48em) {
  body {
    font-size: 16px;
    line-height: 1.4;
  }
}
@media (min-width: 62em) {
  body {
    font-size: 18px;
    line-height: 1.5;
  }
}

main {
  background-color: #edf1f5;
  padding: 8px 12px 8px 12px;
  min-height: 480px;
}

img {
  height: auto;
  display: block;
  width: 100%;
}

button {
  margin: 4px 4px 4px 4px;
  border-radius: 8px;
  border: none;
  display: inline-flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}
button a {
  color: white;
  text-decoration: none !important;
}
button a:hover, button a:active {
  color: white;
}

table {
  border-collapse: separate;
}

table td, table td *, table th, table th * {
  vertical-align: top;
}

h1 {
  font-size: 26px;
  line-height: 1.4;
}
@media (min-width: 0em) {
  h1 {
    font-size: 24px;
    line-height: 1.4;
  }
}
@media (min-width: 48em) {
  h1 {
    font-size: 28px;
    line-height: 1.5;
  }
}
@media (min-width: 62em) {
  h1 {
    font-size: 30px;
    line-height: 1.5;
  }
}

h2 {
  font-size: 22px;
  line-height: 1.4;
}
@media (min-width: 0em) {
  h2 {
    font-size: 20px;
    line-height: 1.4;
  }
}
@media (min-width: 48em) {
  h2 {
    font-size: 24px;
    line-height: 1.5;
  }
}
@media (min-width: 62em) {
  h2 {
    font-size: 26px;
    line-height: 1.5;
  }
}

h3 {
  font-size: 18px;
  line-height: 1.4;
}
@media (min-width: 0em) {
  h3 {
    font-size: 16px;
    line-height: 1.4;
  }
}
@media (min-width: 48em) {
  h3 {
    font-size: 20px;
    line-height: 1.5;
  }
}
@media (min-width: 62em) {
  h3 {
    font-size: 22px;
    line-height: 1.5;
  }
}

h4 {
  font-size: 16px;
  line-height: 1.4;
}
@media (min-width: 0em) {
  h4 {
    font-size: 14px;
    line-height: 1.4;
  }
}
@media (min-width: 48em) {
  h4 {
    font-size: 18px;
    line-height: 1.5;
  }
}
@media (min-width: 62em) {
  h4 {
    font-size: 20px;
    line-height: 1.5;
  }
}

h5 {
  font-size: 14px;
  line-height: 1.4;
}
@media (min-width: 0em) {
  h5 {
    font-size: 12px;
    line-height: 1.4;
  }
}
@media (min-width: 48em) {
  h5 {
    font-size: 16px;
    line-height: 1.5;
  }
}
@media (min-width: 62em) {
  h5 {
    font-size: 18px;
    line-height: 1.5;
  }
}

h6 {
  font-size: 12px;
  line-height: 1.4;
}
@media (min-width: 0em) {
  h6 {
    font-size: 10px;
    line-height: 1.4;
  }
}
@media (min-width: 48em) {
  h6 {
    font-size: 14px;
    line-height: 1.5;
  }
}
@media (min-width: 62em) {
  h6 {
    font-size: 16px;
    line-height: 1.5;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0px 0px 12px 0px;
  text-rendering: optimizelegibility;
}

/*---------------------------
      Icons
---------------------------*/
.category-button {
  padding: 4px 8px;
  font-size: 12px;
  background-color: #4CAF50;
}
.category-button:hover, .category-button:active {
  background-color: #3e8342;
}

.genre-button,
.post-tag-button,
.format-button {
  padding: 4px 8px;
  font-size: 12px;
  background-color: #387ef2;
}
.genre-button:hover, .genre-button:active,
.post-tag-button:hover,
.post-tag-button:active,
.format-button:hover,
.format-button:active {
  background-color: #3344d9;
}

.series-button {
  padding: 4px 8px;
  font-size: 12px;
  background-color: #387ef2;
}
.series-button:hover, .series-button:active {
  background-color: #3344d9;
}

.pagination-button {
  padding: 8px 8px;
  color: #ffffff;
  font-size: 16px;
  height: 32px;
  width: 32px;
  border-radius: 50%;
}

.page-links .post-page-numbers button {
  background-color: #387ef2;
}
.page-links .post-page-numbers button:active, .page-links .post-page-numbers button:hover {
  background-color: #3344d9;
}
.page-links .current button {
  background-color: #3344d9;
}

.blog-links .blog-page-no,
.blog-links .blog-page-prev,
.blog-links .blog-page-next {
  background-color: #387ef2;
}
.blog-links .blog-page-no:active, .blog-links .blog-page-no:hover,
.blog-links .blog-page-prev:active,
.blog-links .blog-page-prev:hover,
.blog-links .blog-page-next:active,
.blog-links .blog-page-next:hover {
  background-color: #3344d9;
}
.blog-links .current .blog-page-no {
  background-color: #3344d9;
}

.edit-button {
  padding: 4px 8px;
  font-size: 12px;
  background-color: #6351ce;
}
.edit-button:active, .edit-button:hover {
  background-color: #6b00b3;
}

.page-title {
  color: #ffffff;
  background-color: #1d2327;
  text-align: center;
}

.sidebar-wrap {
  margin-right: 12px;
}

.content-wrap {
  padding: 16px;
  margin-left: 12px;
  margin-right: 12px;
  border-radius: 8px;
  flex-shrink: 1;
  background-color: #ffffff;
}

.sidebar-wrap {
  padding: 16px;
  border-radius: 8px;
  background-color: #ffffff;
}

.site-content {
  margin-top: 80px;
}

.nav-item:hover .dropdown-menu:not(.show) {
  display: block;
}

.custom-logo-link img {
  max-width: 96px;
  max-height: 60px;
}

.header-nav {
  top: 0;
  width: 100%;
  position: fixed;
  z-index: 1;
}
.header-nav .profile-pic {
  border-radius: 50%;
}
@media (min-width: 0px) {
  .header-nav .profile-pic {
    max-width: 100px;
    max-height: 100px;
  }
}
@media (min-width: 992px) {
  .header-nav .profile-pic {
    max-width: 50px;
    max-height: 50px;
  }
}
.header-nav .nav-link {
  font-weight: 500;
  color: #1d2327 !important;
}
.header-nav .nav-link:hover, .header-nav .nav-link:active {
  color: #387ef2 !important;
}
.header-nav .main-search {
  width: 100%;
}

.footer-nav {
  background-color: #1d2327;
  cursor: default;
}
.footer-nav a {
  color: inherit;
  text-decoration: none;
}
.footer-nav .footer-social {
  background-color: #6351ce;
}
.footer-nav .main-footer .nav-link {
  font-weight: 400;
  color: #ffffff;
}
.footer-nav .main-footer .nav-link:hover, .footer-nav .main-footer .nav-link:active {
  color: #387ef2;
}
.footer-nav .main-footer .footer-separator {
  width: 60px;
  background-color: #387ef2;
  height: 2px;
}
.footer-nav .footer-copyright {
  background-color: #171C1F;
}

.error-page {
  text-align: center;
  background: #ffffff;
  border-top: 1px solid #ffffff;
}
.error-page .error-inner {
  display: inline-block;
}
.error-page .error-inner h1 {
  font-size: 140px;
  text-shadow: 3px 5px 2px rgba(51, 51, 51, 0.2);
  color: #387ef2;
  font-weight: 700;
}
.error-page .error-inner h1 span {
  display: block;
  font-size: 25px;
  color: #1d2327;
  font-weight: 600;
  text-shadow: none;
}
.error-page .error-inner p {
  padding: 20px 15px;
}

.error-back {
  background-color: #387ef2;
  border: none;
  color: #ffffff;
  border-radius: 8px;
  margin: 32px 0px 32px 0px;
  padding: 16px 40px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.error-back:hover, .error-back:active {
  background-color: #3344d9;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.blog-entry {
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(51, 51, 51, 0.2);
  margin-bottom: 16px;
  position: relative;
  top: 0;
  transition: all 0.1s ease-in;
  border-radius: 12px;
  overflow: hidden;
}
.blog-entry .attachment-featured-img {
  padding: 0px;
  max-height: 200px;
  object-fit: cover;
}
.blog-entry .blog-entry-info {
  padding: 8px;
}
.blog-entry .blog-entry-info .blog-entry-title {
  text-decoration: none;
  color: #171C1F;
}
.blog-entry .blog-entry-info .posted-on {
  cursor: default;
}
.blog-entry:hover, .blog-entry:active {
  top: -2px;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

.post-wrap .post-meta {
  margin-bottom: 8px;
}
.post-wrap .post-meta .avatar {
  border-radius: 50%;
}
.post-wrap .page-numbers {
  text-decoration: none;
}

.posted-on,
.posted-by {
  color: #9e9fad;
}

.novel-title a {
  color: #171C1F;
  text-decoration: none;
}

.novel-cover {
  aspect-ratio: 4.1/5.8;
}

.archive-entry-col {
  padding: 0px;
}
.archive-entry-col .archive-entry {
  padding: 0px;
  margin: 4px 8px;
  border-radius: 8px;
  position: relative;
  box-shadow: 0 2px 5px rgba(51, 51, 51, 0.2);
  top: 0;
  transition: all 0.1s ease-in;
  overflow: hidden;
}
.archive-entry-col .archive-entry:hover, .archive-entry-col .archive-entry:active {
  top: -2px;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

.info-section {
  margin-bottom: 16px;
}
.info-section .novel-row {
  margin: 0px;
}
.info-section .novel-row .novel-cover-div {
  background: white;
  border-radius: 8px;
  padding: 8px 12px 0px 12px;
}
@media (min-width: 576px) {
  .info-section .novel-row .novel-cover-div {
    margin-right: 0px;
    margin-bottom: 16px;
  }
}
@media (min-width: 768px) {
  .info-section .novel-row .novel-cover-div {
    margin-right: 16px;
    margin-bottom: 0px;
  }
}
.info-section .novel-row .novel-cover-div .novel-cover {
  border-radius: 8px;
  margin-bottom: 8px;
}
.info-section .novel-row .novel-info {
  background: white;
  border-radius: 8px;
}

.volumes-section {
  background: #ffffff;
  border-radius: 8px;
  padding: 4px;
  margin-bottom: 16px;
}
.volumes-section h2 {
  margin: 8px;
}
.volumes-section .volume-list {
  margin: 0px;
}
@media (min-width: 0px) {
  .volumes-section .volume-list {
    flex-wrap: nowrap;
    overflow-x: auto;
  }
}
@media (min-width: 768px) {
  .volumes-section .volume-list {
    flex-wrap: wrap;
    overflow: auto;
  }
}

.child-section {
  background: #ffffff;
  border-radius: 8px;
  padding: 4px;
  margin-bottom: 16px;
}
.child-section h2 {
  margin: 8px;
}
.child-section .child-list {
  margin: 0px;
}
@media (min-width: 0px) {
  .child-section .child-list {
    flex-wrap: nowrap;
    overflow-x: auto;
  }
}
@media (min-width: 768px) {
  .child-section .child-list {
    flex-wrap: wrap;
    overflow: auto;
  }
}

.related-section {
  background: #ffffff;
  border-radius: 8px;
  padding: 4px;
  margin-bottom: 16px;
}
.related-section h2 {
  margin: 8px;
}
.related-section .child-list {
  margin: 0px;
}
@media (min-width: 0px) {
  .related-section .child-list {
    flex-wrap: nowrap;
    overflow-x: auto;
  }
}
@media (min-width: 768px) {
  .related-section .child-list {
    flex-wrap: wrap;
    overflow: auto;
  }
}

.comment-title {
  color: #ffffff;
  background-color: #1d2327;
  text-align: center;
}

.comments-section {
  padding: 0px 8px;
}
.comments-section .comment-respond .comment-reply-title button {
  border-radius: 50%;
}
.comments-section .comment-respond .comment-form textarea {
  width: 100%;
  border-radius: 12px;
  padding: 8px;
}
.comments-section .comment-respond .comment-form .form-submit input {
  background-color: #3EB2FD;
  background-image: linear-gradient(1deg, #4F58FD, #149BF3 99%);
  background-size: calc(100% + 20px) calc(100% + 20px);
  border: none;
  border-radius: 100px;
  color: #ffffff;
  padding: 6px 20px;
}
.comments-section .comment-list {
  padding-left: 0px;
}
.comments-section .comment-list .comment-entry {
  margin-bottom: 8px;
}
.comments-section .comment-list .comment-entry .comment-header .avatar {
  max-width: 75px;
  max-height: 75px;
  border-radius: 50%;
}
.comments-section .comment-list .comment-entry .comment-body {
  border: 2px solid #9e9fad;
  border-radius: 4px;
}

/* Entry meta */
.updated:not(.published) {
  display: none;
}